.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

/* .....................................
  Mixins for CSS3 Properties
..................................... */
/* helper function for margin and padding
......................................*/
/*
  collection of prefixed flexbox properties
  until we're using Compass 1.0+
  ..........................................*/
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

/*.....................................
  Foundation List Extensions
......................................*/
ul, ol {
  /* no numbers
  ..................................*/
  /* table striped
  ..................................*/ }
  ul.no-numbers, ol.no-numbers {
    list-style: none; }
  ul.list-inside, ol.list-inside {
    list-style-position: inside; }
  ul.striped li, ol.striped li {
    border: none; }
  ul.striped li:nth-child(odd), ol.striped li:nth-child(odd) {
    background: #f8f9fb; }
