.bg-grid-light {
  background-image: url('bg.grid-light.png');
  background-repeat: repeat; }

.bg-grid-dark {
  background-image: url('bg.grid-dark.png');
  background-repeat: repeat; }

.square {
  text-align: center;
  display: inline-block; }
  .square .square-tag {
    padding: 5px;
    border-radius: 3px;
    display: inline-block;
    background-color: #ffffff !important;
    position: relative;
    top: 50%;
    transform: translateY(-50%); }
  .square.square--medium {
    width: 100px;
    height: 100px; }
  .square.square--large {
    width: 120px;
    height: 120px; }
