.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #2d2d2d;
  color: #fff;
  font-size: 12px !important;
  overflow-wrap: break-word !important;
  font-family: Menlo, Monaco, 'Andale Mono', 'Lucida Console', 'Courier New', monospace;
  -webkit-text-size-adjust: none;
  font-weight: bold; }

.hljs-comment, .hljs-title {
  color: #777; }

.code-bar .hljs-variable, .code-bar .hljs-attribute, .code-bar .hljs-tag, .code-bar .hljs-regexp, .code-bar .hljs-name, .code-bar .ruby .constant, .code-bar .xml .tag .title, .code-bar .xml .pi, .code-bar .xml .doctype, .code-bar .html .doctype {
  color: #ab875d; }
.code-bar .css .value {
  color: #cd6a51; }
.code-bar .css .value .function, .code-bar .css .value .string {
  color: #a67f59; }
.code-bar .css .value .number {
  color: #9b869c; }
.code-bar .css .id, .code-bar .css .class, .code-bar .css-pseudo, .code-bar .css .selector, .code-bar .css .tag {
  color: #dfc48c; }
.code-bar .hljs-number, .code-bar .hljs-preprocessor, .code-bar .hljs-built_in, .code-bar .hljs-literal, .code-bar .hljs-params, .code-bar .hljs-constant {
  color: #ab875d; }
.code-bar .ruby .class .title, .code-bar .css .rules .attribute {
  color: #9b869b; }
.code-bar .hljs-string, .code-bar .hljs-value, .code-bar .hljs-inheritance, .code-bar .hljs-header, .code-bar .ruby .symbol, .code-bar .xml .cdata {
  color: #edb46b; }
.code-bar .css .hexcolor {
  color: #cd6a51; }
.code-bar .function, .code-bar .python .decorator, .code-bar .python .title, .code-bar .ruby .function .title, .code-bar .ruby .title .keyword, .code-bar .perl .sub, .code-bar .javascript .title, .code-bar .coffeescript .title {
  color: #fff; }
.code-bar .hljs-keyword, .code-bar .javascript .function {
  color: #8f9c6c; }
.code-bar .hljs-tag {
  color: #d3d0c8;
  font-weight: bold; }
.code-bar .hljs-title {
  color: #f2777a;
  font-weight: bold; }
.code-bar .hljs-attribute {
  color: #ed9157;
  font-weight: bold; }
.code-bar .hljs-value {
  color: #99cc99;
  font-weight: bold; }
