@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

.nav-primary {
  background: #F0F2F4;
  border-right: 1px solid #D7DDE1;
  top: 0;
  left: 0;
  position: fixed;
  overflow-y: auto;
  width: 225px;
  height: 100%; }
  .nav-primary .head.primary {
    color: #869AA5; }
  .nav-primary h2, .nav-primary a {
    text-indent: 30px; }
  .nav-primary a {
    display: block;
    line-height: 40px;
    border-top: 1px solid #D7DDE1; }
    .nav-primary a:link, .nav-primary a:visited {
      color: #869AA5; }
    .nav-primary a:hover {
      background: #D7DDE1; }
    .nav-primary a.is-active {
      background: #22B9B0;
      color: #fff; }
  .nav-primary li:last-child a {
    border-bottom: 1px solid #D7DDE1; }
